import React from "react";
import PropTypes from "prop-types";
import cn from "classnames";
import { Partner } from "./partner";
import style from "./partner-grid.scss";

import gzdnLogo from "../../assets/images/partners/gdn_logo.png";
import innkubatorLogo from "../../assets/images/partners/innkubator_logo.png";
import ycSusLogo from "../../assets/images/partners/yc_sus.png";

const partners = [
  {
    link: "https://www.gruenderzentrum-digitalisierung-niederbayern.de/",
    logo: gzdnLogo,
    name: "Gründerzentrum Digitalisierung Niederbayern"
  },
  { link: "https://www.innkubator.de/", logo: innkubatorLogo, name: "INN.Kubator" },
  { link: "https://www.startupschool.org/companies/vki-fHGba2Z5rA", logo: ycSusLogo, name: "YC StartupSchool" }
];

const PartnerGrid = ({ className }) => (
  <div className={cn(style.root, className)}>
    {partners.map(({ link, logo, name }, idx) => (
      <Partner key={idx} link={link} logo={logo} name={name} />
    ))}
  </div>
);
PartnerGrid.displayName = "PartnerGrid";
PartnerGrid.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string
};
PartnerGrid.defaultProps = {
  children: null,
  className: null
};

export default PartnerGrid;
